.App {
  text-align: center;
  /* padding: 20px; */
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;

}

.Button {
  position: relative;
  display: inline-block;
  margin: 10px;

}

.Divider {
  width: 20px;
  height: 10px;
  display: inline-block;
}

.Wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: 0 auto;
  height: 100%;
  width: 100%;
  resize: both;
  overflow: auto;
  background-color: white;
  transform: translate(-50%, -50%);
}

.ChildWrapper {
  padding: 20px;
}

.mainBody {
  padding: 20px;
}

.ChildWrapper::before,
.ChildWrapper::after {
  position: absolute;
  top: 50%;
  left: 50%;
  padding: 20px;
  clear: both;
  /* transform: translate(-50%, -50%); */
}

.Top-Nav {
  width: 100vw;
}

.MainBody {
  border: 10px solid black;
  height: 400px;
  width: 400px;
}
